export default {
    name: 'Index',

    data() {
        return {
            InfoAbaut: [],
            error: null,
            urlApi: 'http://localhost:1337',
        }
    },
    async fetch() {
        if (process.client) {
            document.body.scrollTop = document.documentElement.scrollTop = 0
        }
        try {
            this.InfoAbaut = await this.$strapi.$about.find()
        } catch (error) {
            this.error = error
        }
